.logo-link {
	margin-left: auto;
	margin-right: 196px;
}

.logo-img {}

.header__tel::before {
	content: "";
	display: block;
	width: 24px;
	height: 24px;
	background-image: url("./../img/icons/phone.svg");
	background-repeat: no-repeat;
	background-size: cover;
	background-position: center;
}

.header__tel {
	font-weight: 500;
	line-height: 1.5;
	display: flex;
	gap: 14px;
	text-decoration-line: underline;
	text-decoration-color: #00000000;
	transition: color 0.3s ease-in, text-decoration-color 0.3s ease-in;
}

.header__tel:hover {
	color: #5B5656;
	text-decoration-line: underline;
	text-decoration-color: #5B5656;
}

@media (max-width:1150px) {
	.header {
		padding: 37px 0 31px;
	}

	.logo-link {
		margin-right: 96px;
	}

	.logo-img {
		max-width: 300px;
		max-height: 59px;
	}

	.header__tel {
		font-size: 18px;
		line-height: 1.33;
		gap: 10px;
	}
}


@media (max-width:750px) {
	.header {
		padding: 30px 0;
	}

	.logo-link {
		margin: unset;
	}

	.logo-img {
		max-width: 300px;
		max-height: 59px;
	}

	.header__tel {
		display: none;
	}
}


@media (max-width:450px) {
	.header {
		padding: 20px 0 15px;
	}

	.logo-link {
		margin: unset;
	}

	.logo-img {
		max-width: 161px;
		max-height: 32px;
	}

	.header__tel {
		display: none;
	}
}